.h12 {
  width: 295px;
  position: relative;
  font-size: var(--font-size-13xl);
  font-weight: 900;
  color: var(--color-black);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button-generate-child {
  width: 350px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-xl);
  background-color: var(--color-black);
  border: 0.3px solid var(--color-black);
  box-sizing: border-box;
  height: 170px;
  z-index: 0;
}
.capa-1-1-icon {
  width: 70px;
  position: relative;
  height: 70px;
  overflow: hidden;
  flex-shrink: 0;
}
.capa-1-1-wrapper {
  width: 155px;
  border-radius: var(--br-xl);
  background-color: var(--color-white);
  border-top: 0.4px solid var(--color-black);
  border-bottom: 0.4px solid var(--color-black);
  border-left: 0.4px solid var(--color-black);
  box-sizing: border-box;
  height: 170px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-41xl) var(--padding-33xl);
  z-index: 1;
}
.div3,
.div4 {
  align-self: stretch;
  position: relative;
  text-align: left;
}
.div3 {
  font-size: var(--font-size-mini);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-white);
}
.div4 {
  font-size: var(--font-size-smi);
  line-height: 100%;
  font-family: var(--font-inter);
  color: var(--color-darkgray-100);
}
.button-generate,
.parent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.parent {
  flex: 1;
  flex-direction: column;
  padding: var(--padding-8xl) 0 0;
  gap: var(--gap-3xs);
  z-index: 2;
}
.button-generate {
  cursor: pointer;
  border: 0;
  padding: 0 0 var(--padding-8xs);
  background-color: transparent;
  align-self: stretch;
  flex-direction: row;
  gap: var(--gap-xs);
}
.button-generate,
.div5,
.div6 {
  position: relative;
}
.div5 {
  align-self: stretch;
  font-weight: 900;
}
.div6 {
  width: 174px;
  font-size: var(--font-size-smi);
  line-height: 100%;
  font-family: var(--font-inter);
  color: var(--color-darkgray-100);
  display: flex;
  align-items: center;
}
.button-paste,
.web {
  align-self: stretch;
  position: relative;
}
.button-paste {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-8xs);
  gap: var(--gap-xs);
  cursor: pointer;
}
.web {
  font-size: var(--font-size-smi);
  line-height: 100%;
  font-family: var(--font-inter);
  color: var(--color-darkgray-100);
}
.h1-group,
.nd-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.h1-group {
  width: 350px;
  gap: var(--gap-2xs);
}
.nd-page {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  height: 844px;
  padding: 84px 0;
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-mini);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
